*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  font-family: "FiraSans", "Arial", sans-serif;
  color: #484848; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.container {
  max-width: 1140px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

@media (max-width: 1023px) {
  .container {
    padding-right: 21px;
    padding-left: 21px; } }

@media (max-width: 767px) {
  .container {
    padding-right: 16px;
    padding-left: 16px; } }

.header {
  position: relative; }

.header .header__bg-header {
  width: 100%;
  position: absolute;
  z-index: -10; }

.top-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 32px; }

.top-block__phone,
.top-block__request-call {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #484848; }

.top-block__request-call {
  text-transform: uppercase;
  border-bottom: 1px dashed #484848; }

@media (max-width: 767px) {
  .top-block {
    position: relative;
    flex-direction: column;
    align-items: flex-end; }
  .top-block__logo-link {
    position: absolute;
    left: 16px; }
  .top-block__logo-img {
    width: 120px;
    height: 42px; }
  .top-block__phone {
    margin-bottom: 10px; } }

.main-block {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 32%; }

.main-block__title {
  display: flex;
  margin-bottom: 32px;
  width: 350px;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }

.main-block__mouse-border {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  border: 1px dashed #ffffff;
  border-radius: 50%;
  padding: 11px; }

.main-block__mouse-pointer {
  position: relative;
  border: 1px solid #ffffff;
  padding: 27px;
  border-radius: 50%; }

.main-block__img::after {
  position: absolute;
  content: "";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 17px;
  height: 26px;
  background-image: url("../img/css_sprites.png");
  background-position: -226px -10px; }

@media (max-width: 1023px) {
  .main-block {
    margin-top: 22%; } }

@media (max-width: 767px) {
  .main-block {
    margin-top: 90%; }
  .main-block__title {
    width: 320px;
    font-size: 20px;
    line-height: 24px; } }
